.Modal {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #ffffff;
  z-index: 2;
  border-radius: 10px;
  padding: 20px;
  outline: none;
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(31, 31, 31, 0.4);
}

.ModalContent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 400px;
  position: relative;
}

.ModalContent h4 {
  text-align: center;
}

.ModalContent i {
  cursor: pointer;
  -webkit-transition: all 350ms ease-out;
  -o-transition: all 350ms ease-out;
  transition: all 350ms ease-out;
}
.ModalContent i:hover {
  color: #f00000;
}

.ModalContent .CloseIcon {
  position: absolute;
  top: -5px;
  right: 0px;
}

.Email {
  padding: 20px;
  background-color: #d3d3d3;
  font-size: 20px;
  border-radius: 5px;
}



