.Wrapper {
  background-color: #eceff1;
  width: 100%;
  min-height: 93vh;
  height: 100%;
  padding-top: 40px;
}

.Wrapper .WaitList {
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
  width: 80%;
  -webkit-box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 10px, rgba(0, 0, 0, 0.23) 0px 3px 10px;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 10px, rgba(0, 0, 0, 0.23) 0px 3px 10px;
  background-color: #fff;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.Wrapper .SingleProduct {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 230px;
  padding: 10px 20px 30px 20px;
  padding-left: 20px !important;
}

.Wrapper .DisabledBtn {
  line-height: 14px !important;
  text-align: center;
  padding-top: 5px;
}

.Wrapper .DisabledBtn i {
  margin-top: 6px;
  margin-right: 8px;
}

.Wrapper .SingleProduct:hover {
  -webkit-box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 10px, rgba(0, 0, 0, 0.23) 0px 3px 10px;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 10px, rgba(0, 0, 0, 0.23) 0px 3px 10px;
}

.Wrapper .SingleProduct .RemoveIcon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  width: 100%;
}

.Wrapper .SingleProduct .RemoveIcon i {
  cursor: pointer;
  -webkit-transition: all 300ms ease-out;
  -o-transition: all 300ms ease-out;
  transition: all 300ms ease-out;
}

.Wrapper .SingleProduct .RemoveIcon i:hover {
  color: tomato;
}

.Wrapper .SingleProduct img {
  height: 100px;
}

.Wrapper .EmptyList {
  padding: 60px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.Name {
  font-size: 17px;
  text-align: center;
}



