.Products ul {
  max-width: 815px;
}

.Prodoct_Item {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 250px;
  height: 420px;
  -webkit-box-shadow: 0px 0px 12px 0px rgba(4, 8, 7, 0.59);
          box-shadow: 0px 0px 12px 0px rgba(4, 8, 7, 0.59);
  margin: 5px;
