.Prodoct_Item .BtnBuy {
  display: inline-block;
  width: 70%;
  font-size: 12px;
}

.Prodoct_Item .BtnBuy:hover {
  background-color: #64dd17 !important;
}

.Prodoct_Item .BtnBuy i {
  -webkit-transition: all 250ms ease-out;
  -o-transition: all 250ms ease-out;
  transition: all 250ms ease-out;
}

.Prodoct_Item .BtnBuy:hover i {
  color: #000;
}

.Prodoct_Item .BtnJoin {
  background-color: #01579b !important;
}

.Prodoct_Item .BtnWait {
  font-size: 12px;
  position: relative;
  display: inline-block;
  width: 70%;
  background-color: #fafafa !important;
}
.Prodoct_Item .BtnWait i {
  position: absolute;
  top: 0px;
  left: 11px;
  color: #000;
}

.Prodoct_Item .BtnOver {
  background-color: #d3d3d3 !important;
  font-size: 12px;
  position: relative;
  padding-left: 40px;
}

.Prodoct_Item .BtnOver i {
  position: absolute;
  top: 0px;
  left: 11px;
}

.Prodoct_Item .BtnWait span {
  position: absolute;
  top: 5px;
  left: 45px;
  line-height: 14px;
  color: #000;
}
.Prodoct_Item .Price {
  font-size: 18px;
  padding: 5px;
}

.Prodoct_Item .DiscountCont {
