  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.Prodoct_Item .OldPrice {
  position: relative;
}
.Prodoct_Item .OldPrice::before {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  right: 0;
  border-top: 1px solid #ff0000;
  color: #474747;
}

.Prodoct_Item .Discount {
  background-color: #fff3b5;
  padding: 5px 10px;
  color: #ff0000;
}

.Prodoct_Item .Descr {
  text-align: center;
  padding: 0 20px;
  font-size: 12px;
  overflow: hidden;
  height: 52px;
  line-height: 25px;
}

.Prodoct_Item .Descr p {
  margin: 0;
  font-weight: 500;
}

.Prodoct_Item .Descr:before {
  content: "";
  float: left;
  width: 5px;
  height: 52px;
}

.Prodoct_Item .Descr > *:first-child {
  float: right;
  width: 100%;
  margin-left: -5px;
}

.Prodoct_Item .Descr:after {
  content: "\02026";
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  float: right;
  position: relative;
  top: -28px;
  left: 95%;
  width: 3em;
  margin-left: -3em;
  padding-right: 5px;
  text-align: right;
  background-size: 100% 100%;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(255, 255, 255, 0.2)),
    color-stop(50%, white),
    to(white)
  );
  background: -webkit-linear-gradient(
    left,
    rgba(255, 255, 255, 0.2),
    white 50%,
    white
  );
  background: -o-linear-gradient(
    left,
    rgba(255, 255, 255, 0.2),
    white 50%,
    white
  );
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0.2),
    white 50%,
    white
  );
}

.Prodoct_Item .Descr .Descr_In {
  display: inline-block;
}

.Tags {
  position: fixed;
  top: 15%;
  left: 20px;
  width: 150px;
}
.Pagination {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.Pagination div {
  display: inline-block;
}

@media screen and (max-width: 519px) {
  .Tags {
    display: none;
  }
  .Products {
    margin-left: 0;
  }
  .Products ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
}



