.AddMoreProducts {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 20px;
}

.AddMoreProducts img {
  height: 100px;
}
.SingleProduct {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.SingleProduct .DelComp {
  -webkit-transition: all 250ms ease-out;
  -o-transition: all 250ms ease-out;
  transition: all 250ms ease-out;
  border-radius: 50%;
  border: 1px solid #d5d5d5;
  padding: 3px;
}
.SingleProduct .DelComp:hover {
  color: #f2f2f2;
  cursor: pointer;
  background-color: #e65959;
  border: 1px solid transparent;
}
.SingleProduct .Descr {
  width: 120px;
  height: 70px;
  overflow: hidden;
  font-size: 12px;
  text-align: center;
}
@media screen and (max-width: 700px) {
  .AddMoreProducts h2 {
    font-size: 30px;
  }
  .AddMoreProducts h4 {
    font-size: 24px;
  }
}



